import React from 'react';
import { Router , Route, Switch } from "react-router-dom";
import { createBrowserHistory } from "history";

import "./style.scss";

import Devices from "./views/Devices/index.jsx";
import SingleDevice from "./views/SingleDevice/index.jsx";
import Clients from "./views/Clients/index.jsx";
import Accounts from "./views/Accounts/index.jsx";
import TempAccess from "./views/TempAccess/index.jsx";

import Header from "./components/Header/index.jsx";
import LeftBar from "./components/LeftBar/index.jsx";

function App() {
	const customHistory = createBrowserHistory();

	return (
		<Router history={customHistory}>
			{customHistory.location.pathname !== "/temporary-access" ? <div className="wrapp">
				<Header/>
				
				<div className="view-wrapp">
					<LeftBar/>
					
					<div className="view">
						<Switch>
							<Route path="/devices" component={Devices}/>
							<Route path="/device" component={SingleDevice}/>
							<Route path="/clients" component={Clients}/>
							<Route path="/accounts" component={Accounts}/>
						</Switch>
					</div>
				</div>
			</div>
			:
				<div className="wrapp">
					<Header guest={true}/>
					<TempAccess/>
				</div>
			}

		</Router>
	);
}

export default App;
